import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Button, Menu, Icon} from 'antd';
import { CSSTransition,TransitionGroup} from 'react-transition-group';
import { createStore } from 'redux';

import './css/index.scss';
import './font/iconfont.css';
import registerServiceWorker from './registerServiceWorker';

const reducer = (state={isNavCur:false},action)=> {
	switch(action.type) {
		case 'toggleNav':
			return {...state,isNavCur:(state.isNavCur?false:true)};
		default:
			return state;
	}
}
const store = createStore(reducer);

class App extends Component {
  render() {
    return (
      <div>
      	<NavBar />
      	<MainScreen />
      </div>
    )
  }
}

class NavBar extends Component { 
	componentWillMount() {
		this.unsubscribe = store.subscribe(() => {
			this.forceUpdate();
		})
	}
    render() {
		return (
			<section className={store.getState().isNavCur?'navContainer cur':'navContainer'}>
				<ul className="navList">
					<li>
						<a href="intro">扉页
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
					<li>
						<a href="intro">技能
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
					<li>
						<a href="intro">作品
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
					<li>
						<a href="intro">关于我
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
					<li>
						<a href="intro">联系方式
							<i className="iconfont icon-angle-right"></i>
						</a>
					</li>
				</ul>
			</section>
		)
	}
	componentWillUnmount() {
		this.unsubscribe();
	}
}
class MainScreen extends Component {
	componentWillMount() {
		this.unsubscribe = store.subscribe(() => {
			this.forceUpdate();
		})
	}
	handleNavToggle = (e) => {
  		store.dispatch({type:'toggleNav'});
 		console.log(store.getState().isNavCur)
  	}
	render() {
		return (
			<section className="mainScreen">
				<img src={require("./images/main/mainPic.jpg")} alt=""/>
				<div id="toggleNav" onClick={this.handleNavToggle}><p>click me~</p></div>
				<CSSTransition
	                in={true}
	                timeout={600}
                    appear={true}
                    classNames="signature"
	            >
			        <div className="signature">lxltmx</div>
			    </CSSTransition>
			    <CSSTransition
	                in={true}
	                timeout={1600}
                    appear={true}
                    classNames="subtitle"
	            >
			        <div className="subtitle">follow the heart</div>
			    </CSSTransition> 
			</section>
		)
	}
}

ReactDOM.render(<App />, document.getElementById('page'));
registerServiceWorker();






